.navbar {
    width: 250px;
    background-color: #282c34;
    color: white;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    padding-top: 20px;
}

.navbar-title {
    margin-bottom: 30px;
    font-size: 24px;
    font-weight: bold;
}

.navbar-list {
    list-style-type: none;
    padding: 0;
    width: 100%;
    margin: 0;
}

.navbar-item {
    width: 100%;
}

.navbar-link {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    transition: background-color 0.2s;
}

.navbar-link:hover {
    background-color: #3b3f47;
}

.navbar-icon {
    margin-right: 10px;
}


.content {
    margin-left: 250px; /* NavBar의 너비만큼 여백 */
    padding: 20px; /* 추가적인 패딩을 사용하여 내부 여백 설정 */
    flex-grow: 1; /* 콘텐츠가 남은 공간을 채우도록 설정 */
}

